let cardSortKeys: [string, string?][] = [];
export const CARD_SORT_KEY_MAP: { [cardId: string]: string | undefined } = {};

for (let cardSet of typedObjectKeys(CARD_DATA)) {
  for (let cardId of typedObjectKeys(CARD_DATA[cardSet]) as TCardId[]) {
    let card = CARD_DATA[cardSet][cardId];
    if (
      cardId === CardId.KIRIKO_O_S_1 ||
      cardId === CardId.HATSUMI_A1_D_1 ||
      cardId === CardId.HATSUMI_A1_D_2
    ) {
      // 
      card.sortKey === `99-${cardId}`;
    } else if (card.extraFrom) {
      // 
      card.sortKey = `${
        CARD_DATA[cardSet as CardSet][card.extraFrom].sortKey
      }_${cardId}`;
    } else if (card.replace) {
      // 
      card.sortKey = `${card.replace}_${card.anotherID}`;
    } else if (card.baseType === "poison") {
      //  ()
      card.sortKey = `99-${cardId}`;
    } else {
      // 
      card.sortKey = cardId;
    }

    cardSortKeys.push([cardId, card.sortKey]);
    CARD_SORT_KEY_MAP[cardId] = card.sortKey;
  }
}

// ID
let sortedAllCardIds = sortedUniq(
  sortBy(cardSortKeys, (p) => p[1]).map((p) => p[0])
) as TCardId[];

// ID
export const ALL_CARD_ID_LIST: { [cardSet in CardSet]: TCardId[] } = {} as {
  [cardSet in CardSet]: TCardId[];
};
export const ALL_CARD_LIST: { [cardSet in CardSet]: CardDataItem[] } = {} as {
  [cardSet in CardSet]: CardDataItem[];
};

for (let cardSet of typedObjectKeys(CARD_DATA) as CardSet[]) {
  ALL_CARD_ID_LIST[cardSet as CardSet] = [];
  ALL_CARD_LIST[cardSet as CardSet] = [];

  for (let cardId of sortedAllCardIds) {
    if (CARD_DATA[cardSet][cardId]) {
      ALL_CARD_ID_LIST[cardSet as CardSet].push(cardId);
      ALL_CARD_LIST[cardSet as CardSet].push(
        CARD_DATA[cardSet as CardSet][cardId]
      );
    }
  }
}
